import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        topics: [
            // tab切换
            { name: "all", desc: "全部" },
            { name: "good", desc: "精华" },
            { name: "share", desc: "分享" },
            { name: "ask", desc: "问答" },
            { name: "job", desc: "招聘" },
            { name: "dev", desc: "测试" }
        ],
        isShowMoreMenus: false, //actionsheet默认显示与否
        // moreMenus: //如果是未登入状态，显示‘登录’,如果是已登录状态,显示个人中心,消息通知,退出
        isLogined: false, //登录状态，默认未登录
        unLoginedMenus: {
            login: '登录'
        },
        loginedMenus: {
            center: '个人中心',
            create: '发表新话题',
            msg: '消息通知',
            logout: '退出'
        },
        loginname: '', //登录用户名
        accesstoken: '', //登录的accesstoken
        authorId: '', //作者ID
    },
    getters: {
        moreMenus(state) {
            if (state.isLogined) {
                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations: {
        //修改actionsheet的显示与否
        mutationIsShowMoreMenus: function(state, payload) {
            state.isShowMoreMenus = payload;
        },
        //修改登录状态
        mutationIsLogined: function(state) {
            state.isLogined = true
        },
        //修改登录名
        mutationIsLogined: function(state) {
            state.isLogined = true;
        },
        //修改登录accesstoken账号
        mutationAccesstoken: function(state, payload) {
            state.accesstoken = payload;
        },
        //修改登录账号id
        mutationAuthorId: function(state, payload) {
            state.authorId = payload;
        },
        //退出操作
        mutationLogout: function(state) {
            state.isLogined = false;
            state.loginname = "";
            state.accesstoken = '';
            state.authorId = '';
        }

    },
    actions: {}
});
export default store;


